    <template>
        <div class="container select2">
            <div class="container">
                <div v-scroll-spy class="row layout-top-spacing">
                    <div id="fuSingleFile" class="col-lg-12 layout-spacing">
                        <div class="statbox panel box box-shadow">
                            <div class="panel-heading">
                                <div class="row">
                                    <div class="col-xl-12 col-md-12 col-sm-12 col-12">
                                        <h4>Program Enrollment Summary</h4>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            
                <div class="row">
                    <div class="col-md-6 panel-body statbox  box box-shadow">
                        <div class="form-group mb-4 panel-body">
                            <label for="programs">Programs</label>
                            <multiselect 
                                v-model="selectedProgram" 
                                :options="programs" 
                                :searchable="true" 
                                :preselect-first="true"
                                selected-label="" 
                                select-label="" 
                                deselect-label="">
                            </multiselect>

                        </div>
                    </div>
                </div>
                <div class="row">
                    <div class="col-md-12 panel-body statbox box box-shadow d-flex justify-content-end">

                        <!-- <button  type="button"  @click="exportToExcel"  class="btn btn-success btn-lg mb-4 me-2">Export </button> -->
                        <button @click="filterByProgram" type="button"
                            class="btn btn-danger btn-lg mb-4 me-2">Filter</button>
                    </div>
                </div>

                <!-- Tabla para mostrar los datos de los programas -->
                <div class="row layout-top-spacing">
        <div class="col-xl-12 col-lg-12 col-sm-12 layout-spacing">
                    <div class="panel p-0">
                        <div class="custom-table">
                            <v-client-table :data="tableData" :columns="columns" :options="tableOptions">
                            </v-client-table>
                        </div>
                    </div>
                </div>
            </div>

        
            </div>
        </div>
    </template>

    <script setup>
    import { ref, onMounted, computed, watch } from 'vue';
    import '@/assets/sass/forms/file-upload-with-preview.min.css';
    import * as XLSX from 'xlsx';
    import FileUploadWithPreview from 'file-upload-with-preview';

    // flatpickr
    import flatPickr from 'vue-flatpickr-component';
    import 'flatpickr/dist/flatpickr.css';
    import '@/assets/sass/forms/custom-flatpickr.css';

    //multiselect
    import Multiselect from '@suadelabs/vue3-multiselect';
    import '@suadelabs/vue3-multiselect/dist/vue3-multiselect.css';
    import '@/assets/sass/scrollspyNav.scss';
    import { useStore } from 'vuex';

    const store = useStore();

    const excelDataTemp = ref([]); 
    onMounted(() => {
        loadExcelData(() => {
            console.log("Todos los datos se han cargado."); // Mensaje en la consola
            generateJSON()
        });
    });


    const loadExcelData = (callback) => {
        const defaultFilePath = process.env.NODE_ENV === 'production'
            ? '/GDE3/InActiveStuCourseList.xlsx'
            : '/InActiveStuCourseList.xlsx';

        fetch(defaultFilePath)
            .then(response => response.arrayBuffer())
            .then(data => {
                const workbook = XLSX.read(data, { type: 'array' });
            const worksheet = workbook.Sheets[workbook.SheetNames[0]];
            const rawData = XLSX.utils.sheet_to_json(worksheet);
            
            // Filtrar filas innecesarias antes de transformarlas
            excelDataTemp.value = rawData.filter(row => row['__EMPTY_8'] === 'Dropped');
            excelDataOriginal.value = [...excelDataTemp.value];
                
                if (callback && typeof callback === 'function') {
                    callback(); // Ejecuta el callback al finalizar
                }
            })
            .catch(error => console.error("Error al cargar el archivo:", error));
    };

    const groupedData = {};

    const generateJSON = () => {
    
    const gradeValues = { A: 4, B: 3, C: 2, D: 1, F: 0, TR: 0 };

    excelDataOriginal.value.forEach(row => {
        const programName = row['__EMPTY_4'];
        const programVersions = store.getters.getProgramsVersions || {};
        const creditsMap = store.getters.getProgramCreditsMap || {};
        const transformedProgram = Object.keys(programVersions).find(key =>
            programVersions[key].includes(programName)
        ) || programName;

        const primaryKey = `${row['__EMPTY_1']}-${transformedProgram}-${row['__EMPTY_18']}-${row['__EMPTY_8']}`;
        if (!groupedData[primaryKey]) {
            groupedData[primaryKey] = {
                Student: row['__EMPTY_1'],
                Program: transformedProgram,
                "Term Started": row['__EMPTY_18'],
                Status: row['__EMPTY_8'],
                Credits: creditsMap[transformedProgram] || 0,
                Degree: row['__EMPTY_6'],
                ApprovedCourses: 0,
                TotalGradePoints: 0,
                EvaluatedCourses: 0
            };
        }

        const grade = row['__EMPTY_17']?.trim().toUpperCase();
        if (isCourseApproved(row['__EMPTY_6'], grade)) {
            groupedData[primaryKey].ApprovedCourses += 1;
            groupedData[primaryKey].TotalGradePoints += gradeValues[grade] || 0;
            groupedData[primaryKey].EvaluatedCourses += 1;
        }
    });

    Object.values(groupedData).forEach(entry => {
        entry.GPA = entry.EvaluatedCourses > 0
            ? Math.round((entry.TotalGradePoints / entry.EvaluatedCourses) * 100) / 100
            : 0;
    });

    tableData.value = Object.values(groupedData)
        .filter(item => item.Status === 'Dropped')
        .map((item, index) => ({
            No: index + 1,
            Student: item.Student,
            Program: item.Program,
            "Term Started": item["Term Started"],
            Credits: item.Credits,
            ApprovedCourses: item.ApprovedCourses * 3,
            "% Earned": item.Credits > 0
                ? Math.ceil((item.ApprovedCourses * 3 * 100) / item.Credits)
                : 0,
            GPA: item.GPA
        }));
};


const filterByProgram = () => {
    if (selectedProgram.value === "All") {
        // Mostrar todos los programas
        tableData.value = Object.values(groupedData)
            .filter(item => item.Status === 'Dropped')
            .map((item, index) => ({
                No: index + 1,
                Student: item.Student,
                Program: item.Program,
                "Term Started": item["Term Started"],
                Credits: item.Credits,
                ApprovedCourses: item.ApprovedCourses * 3,
                "% Earned": item.Credits > 0
                    ? Math.ceil((item.ApprovedCourses * 3 * 100) / item.Credits)
                    : 0,
                GPA: item.GPA
            }));
    } else {
        // Filtrar por el programa seleccionado
        tableData.value = Object.values(groupedData)
            .filter(item => item.Program === selectedProgram.value && item.Status === 'Dropped')
            .map((item, index) => ({
                No: index + 1,
                Student: item.Student,
                Program: item.Program,
                "Term Started": item["Term Started"],
                Credits: item.Credits,
                ApprovedCourses: item.ApprovedCourses * 3,
                "% Earned": item.Credits > 0
                    ? Math.ceil((item.ApprovedCourses * 3 * 100) / item.Credits)
                    : 0,
                GPA: item.GPA
            }));
    }
};







const columns = ref(["No", "Student", "Program", "Term Started", "Credits", "ApprovedCourses","% Earned", "GPA"]);

    const tableData = ref([]); // Datos que se mostrarn en la tabla


    const items = ref([]);  // Datos finales que se mostrarn en la tabla

    const selectedProgram = ref("All");




    const options1 = ref(["41", "24", "20", "15", "14", "12", "8", "6", "5", "4"]);

    const programs = computed(() => {
    const allPrograms = store.getters.getAllPrograms || [];
    return ["All", ...allPrograms]; // Agrega "All" como la primera opcin
});

    // Funcin para determinar si un curso est aprobado
const isCourseApproved = (degree, grade) => {
    const masterApprovedGrades = ["A", "B", "C","TR"];
    const associateBachelorApprovedGrades = ["A", "B", "C", "D","TR"];

    if (degree === "Master Degree") {
        return masterApprovedGrades.includes(grade);
    } else if (degree === "Associate Degree" || degree === "Bachelor Degree") {
        return associateBachelorApprovedGrades.includes(grade);
    }
    return false; // No aprobado si no coincide con ninguna regla
};


const gradeValues = {
    A: 4,
    B: 3,
    C: 2,
    D: 1,
    F: 0,
    TR: 0 // Transfer no tiene peso
};


    const tableOptions = ref({
        perPage: 10,
        perPageValues: [5, 10, 20],
        skin: "table",
        pagination: { nav: "scroll", chunk: 5 },
        sortable: ["No", "Student", "Program", "Term Started", "Status", "Credits", "% Earned"]
    });

    const excelDataOriginal = ref([]);  // Variable para almacenar una copia de todos los datos originales del Excel






    </script>
